@import "../../global.scss";

.works{
    background-image: url(https://mangathrill.com/wp-content/uploads/2021/04/pjimage-2021-04-03T004414.243.jpg);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-repeat: no-repeat;
    background-size: 100% 100%;


    .arrow{
        height: 50px;
        position: absolute;
        cursor: pointer;
        background-color: white;

    @include mobile{
        display: none;
    }


        &.left{
            left: 100px;
            transform: rotateY(360deg);
        }

        &.right{
            transform: rotateY(180deg);
            right: 100px;

        }
    }
    
    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease;
        
        @include mobile{
            height: 100vh;
            flex-direction: column;
            justify-content: center;

        }

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width: 800px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include mobile{
                    width: 90%;
                    height: 180px;
                    margin: 5px 0;
                    
                }

                .anh{
                   
                    flex: 2;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    img{
                        width:100%;
                        height: 100%;
                        overflow: hidden;
                        
                    }
                }
              
            }
      }
    }
    
    
}