@import "../../global.scss";

   

.contact{   
   text-shadow: 2px 2px black;
    display: flex;
    flex-direction: column;
    align-items: center;
    color:white;
    padding-top: 20px;
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;
    object-fit: cover;
    background-size: 100% 100%;

    
    @include mobile{
        flex-direction: column;
        font-size: 15px;
        background-size: 100% 100%;
    }

    .about{
        position: absolute;
        margin-top: 440px;
        text-align: center;
        color:white;

        @include mobile{
            font-size: 19px;
            margin-top: 380px;
        }

        a:link {
            color: white;
            background-color: transparent;
            
          }
          a:visited {
            color: white;
            background-color: transparent;
            
          }
       

        #facebook:hover {
            background-color: rgb(112, 112, 233);
        }
        #instagram:hover{
            background-color: orange;
        }
          
    }
    
}