@import "../../global.scss";

.intro{
  
    background-color: white;
    display: flex;
    
    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    .left{
        flex: 0.5;
        overflow: hidden;
        align-items: center;
        justify-content: center;
        .imgContainer{
        
            width: 700px;
            height: 700px;
    
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;
            

            @include mobile{
                align-items: flex-start;
            }
            img{
                height: 90%;

                @include mobile{
                    height: 50%;
                }
            }
        }
      

    }
    .right{
        flex:0.5;
        position: relative;
        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            
            @include mobile{
                padding-left: 0;
                align-items: center;
            }
      

            /*-----------------------------------*/
            span{
                font-size: 42px;
            }
            #Vword{
                color: #C82032;
            }
            #Kword{
                color: #F7961E;
            }
            #Uword{
                color: #374F8A;
            }
            
            /*-----------------------------------*/

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
                margin-left: 250px;
                @include mobile{
                    margin-left: 0;
                    font-size: 20px;
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 37%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;


            }
            @keyframes arrowBlink {
                100%{
                    opacity: 0.4; 
                }
                
            }
        }
    }
}