@import "../../global.scss";

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:0;
    padding: 0;
    h1{
        padding-top: 30px;
        font-size: 50px;
        @include mobile{
            font-size: 20px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        
        @include mobile{
            margin: 100px 0;
            flex-wrap: wrap;
            justify-content: center;
        }
        li{
            font-size: 12px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active{
                background-color: $mainColor;
                color: white;
            }

        }

    }
    .container{
        padding-top: 60px;
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        
        @include mobile{
             width: 400px;
             height: 350px; 
        } 

        img{
            width: 70%;
            height: 70%;

        }
        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid lightgrey;
            margin: 10px 20px;
            display: flex;
            align-items: center;    
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
               
                
                width: 120px;
                height: 80px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
                overflow: hidden;
            }
            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}
